.Header {
  position: absolute;
  right: 30px;
  top: 0;
  z-index: 100;
}

.Header .Header__Inner {
  transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  -ms-transition: all .5s ease-in-out;
}

.Header .Back__Home {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  -ms-transition: all .5s ease-in-out;
}

.Header.on .Back__Home {
  visibility: visible;
  opacity: 1;
}

.Header.on .Header__Inner {
  visibility: hidden;
  opacity: 0;
}

.gnb ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.gnb ul:after {
  display: block;
  clear: both;
  content: ''
}

.gnb ul li {
  float: left;
  margin: 0 15px;
  opacity: .5;
  line-height: 60px;
  cursor: pointer;
}

.gnb ul li.on {
  opacity: 1;
}

.Search__Wrap {
  margin-right: 50px;
  display: inline-block;
  vertical-align: middle;
}

.Search__Wrap input {
  height: 38px;
  line-height: 38px;
  border: none;
  background: transparent;
  color: #ccc;
  font-size: 16px;
  padding: 0 10px;
  text-align: center;
  margin: 5px 0;
  font-family: 'Noto Sans KR', sans-serif;
  width: 150px;
  outline: none;
  transition: background 0.5s ease-in-out;
  -webkit-transition: background .5s ease-in-out;
  -moz-transition: background .5s ease-in-out;
  -o-transition: background.5s ease-in-out;
  -ms-transition: background .5s ease-in-out;
}

.Search__Wrap input:focus {
  background: rgba(0, 0, 0, .3);
}

.fa-search {
  font-size: 18px;
  color: #ccc;
  cursor: pointer;
}

.Header .Header__Inner > div {
  display: inline-block;
  vertical-align: middle;
}